import React from 'react';
import logo from './../images/Vector.svg'; // –ü—É—Ç—å –∫ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—é –≤–Ω—É—Ç—Ä–∏ —Å–±–æ—Ä–∫–∏
import { Link, Route, Switch} from 'react-router-dom';

function Header({email, onSignOut}) {

  return (
    <header className="header root__section">

      <div className="logo__text">MESTO</div>
      <Switch >
      <Route path = "/Auth/sign-in"><Link to="/Auth/sign-up" className="header__text">Registrieren</Link></Route>
      <Route path = "/Auth/sign-up"><Link to="/Auth/sign-in" className="header__text">Login</Link></Route> 
      <Route path = "/Auth/">
        <div className="header__elements">
        <div className="header__text">{email}</div>
        <div className="header__signout" onClick={onSignOut}>Logout</div>
        </div>
      </Route> 
      </Switch >
    </header>
  );
}

export default Header;