import React from "react";
import './../pages/index.css';
import Header from './Header';
import Main from './Main';
import Login from './Login';
import Register from './Register';
import InfoTooltip from './InfoTooltip';
import ProtectedRoute from './ProtectedRoute';
import * as Auth from '../utils/Auth';

import union from './../images/Union.png';
import union2 from './../images/Union2.svg';

import ImagePopup from './ImagePopup';
import api from "./../utils/api";
import EditProfilePopup from "./EditProfilePopup";
import EditAvatarPopup from "./EditAvatarPopup";
import AddPlacePopup from "./AddPlacePopup";
import { CurrentUserContext } from "./../contexts/CurrentUserContext";
import { Route, Switch, withRouter, useHistory } from 'react-router-dom';

function App() {
    const history = useHistory();
    // –•—É–∫, —É–ø—Ä–∞–≤–ª—è—é—â–∏–π –≤–Ω—É—Ç—Ä–µ–Ω–Ω–∏–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ–º
    const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = React.useState(false);
    const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = React.useState(false);
    const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = React.useState(false);
    const [isInfoTooltipOpen, setIsInfoTooltipOpen] = React.useState(false);

    const [selectedCard, setSelectedCard] = React.useState({});
    const [currentUser, setCurrentUser] = React.useState({});
    const [cards, set–°ards] = React.useState([]);
    const [loggedIn, setLoggedIn] = React.useState(false);
    const [myemail, setMyemail] = React.useState("");

    const [text, setText] = React.useState("");
    const [logo, setLogo] = React.useState("");

    React.useEffect(() => {
        api.getUserInfo()
            .then((user) => {
                setCurrentUser(user);
            })
            .catch((err) => {
                console.log(err);
            })
    }, []);


    React.useEffect(() => {
        const handleEsc = (event) => {
            if (event.key === 'Escape') {
                closeAllPopups();
            }
        };
        document.addEventListener('keydown', handleEsc);
        return () => {
            document.removeEventListener('keydown', handleEsc);
        };
    }, []);

    React.useEffect(() => {
        const closeByOverlay = (evt) => {
            if (evt.target.classList.contains('popup')) {
                closeAllPopups();
            }
        }
        document.addEventListener('click', closeByOverlay);

        return () => {
            document.removeEventListener('click', closeByOverlay);
        };
    }, []);


    React.useEffect(() => {
        api.getInitialCards()
            .then((res) => {
                set–°ards(res);
            })
            .catch(err => console.log(err))
    }, [])


    function handleUpdateUser(profile) {
        api.setUserInfo(profile.name, profile.about)
            .then((user) => {
                setCurrentUser(user);
                closeAllPopups();
            })
            .catch((err) => {
                console.log(err);
            })
    }

    function handleUpdateAvatar(url) {
        api.setUserAvatar(url.avatar)
            .then((user) => {
                const copy = Object.assign({}, currentUser);
                copy.avatar = user.avatar;
                setCurrentUser(copy);
                closeAllPopups();
            })
            .catch((err) => {
                console.log(err);
            })
    }

    function handleAddPlaceSubmit(card) {
        api.addCard(card.name, card.link)
            .then((newCard) => {
                set–°ards([newCard, ...cards]);
                closeAllPopups();
            })
            .catch((err) => {
                console.log(err);
            })
    }

    function handleCardClick(card) {
        setSelectedCard(card);
    }

    function handleEditAvatarClick() {
        setIsEditAvatarPopupOpen(true);
    }

    function handleEditProfileClick() {
        setIsEditProfilePopupOpen(true);
    }

    function handleAddPlaceClick() {
        setIsAddPlacePopupOpen(true);
    }

    function handleLoginClick(value) {
        setIsInfoTooltipOpen(value);
    }

    function closeAllPopups() {
        setIsEditAvatarPopupOpen(false);
        setIsEditProfilePopupOpen(false);
        setIsAddPlacePopupOpen(false);
        setSelectedCard({});
    }
    function closePopupNew() {
        setIsInfoTooltipOpen(false);
        history.push('/sign-in');
    }


    function handleCardLike(card) {
        // –°–Ω–æ–≤–∞ –ø—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ –ª–∞–π–∫ –Ω–∞ —ç—Ç–æ–π –∫–∞—Ä—Ç–æ—á–∫–µ
        const isLiked = card.likes.some(i => i._id === currentUser._id);
        // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –≤ API –∏ –ø–æ–ª—É—á–∞–µ–º –æ–±–Ω–æ–≤–ª—ë–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –∫–∞—Ä—Ç–æ—á–∫–∏
        api.changeLikeCardStatus(card._id, !isLiked)
        .then((newCard) => {
            // –§–æ—Ä–º–∏—Ä—É–µ–º –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –Ω–∞ –æ—Å–Ω–æ–≤–µ –∏–º–µ—é—â–µ–≥–æ—Å—è, –ø–æ–¥—Å—Ç–∞–≤–ª—è—è –≤ –Ω–µ–≥–æ –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É –∏ –æ–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç
            set–°ards((state) => cards.map((c) => c._id === card._id ? newCard : c));
        })
        .catch((err) => {
            console.log(err);
        });
    }

    function handleCardDelete(card) {
        api.deleteCard(card._id)
            .then(() => {
                // –§–æ—Ä–º–∏—Ä—É–µ–º –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –Ω–∞ –æ—Å–Ω–æ–≤–µ –∏–º–µ—é—â–µ–≥–æ—Å—è, —É–¥–∞–ª—è—è –∏–∑ –Ω–µ–≥–æ –∫–∞—Ä—Ç–æ—á–∫—É card._id
                const newCards = cards.filter(function (c) {
                    return c._id !== card._id;
                });
                // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç
                set–°ards(newCards);
            })
            .catch((err) => {
                console.log(err);
            })
    }


    function handleLogin() {
        setLoggedIn(true);
    }

    const onRegister = (password, email) => {
        Auth.register(password, email)
        .then((res) => {
                if (res) {
                    setText("Erfolgreich registriert!");
                    setLogo(union);
                    handleLoginClick(true);
                }
            })
            .catch((err) => {
                    setText("Oops, etwas ist schief gelaufen.");
                    setLogo(union2);
                    handleLoginClick(true);
            })
    }

    const onLogin = (password, email) => {
        Auth.authorize(password, email)
            .then((data) => {
                if (data.token) {
                    localStorage.setItem('jwt', data.token);
                    handleEmail(email);
                    email = '';
                    password = '';
                    handleLogin(); // –æ–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç –≤–Ω—É—Ç—Ä–∏ App.js
                    history.push('/'); // –∏ –ø–µ—Ä–µ–∞–¥—Ä–µ—Å—É–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è! 
                }
            })
            .catch((err) => {
                console.log("hallo");
                setText("Login leider nicht erfolgreich.");
                setLogo(union2);
                handleLoginClick(true);
        })
    }


    const tokenCheck =() =>{
        // –µ—Å–ª–∏ —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –µ—Å—Ç—å —Ç–æ–∫–µ–Ω –≤ localStorage,
        // —ç—Ç–∞ —Ñ—É–Ω–∫—Ü–∏—è –ø—Ä–æ–≤–µ—Ä–∏—Ç –≤–∞–ª–∏–¥–Ω–æ—Å—Ç—å —Ç–æ–∫–µ–Ω–∞
        const jwt = localStorage.getItem('jwt');
        if (jwt) {
            // –ø—Ä–æ–≤–µ—Ä–∏–º —Ç–æ–∫–µ–Ω
            Auth.getContent(jwt)
            .then((res) => {
                if (res) {
                     handleEmail(res.data.email);
                    // –∞–≤—Ç–æ—Ä–∏–∑—É–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
                    setLoggedIn(true);
                    history.push("/Auth");
                }
            })            
            .catch((err) => {
                console.log(err);
            })
        }
    }

    React.useEffect(() => {
        tokenCheck()
    }, []);

    function handleEmail(email) {
        setMyemail(email);
    }
    function onSignOut() {
        localStorage.removeItem('jwt');
        handleEmail('');
        history.push('/Auth/sign-in');
    }

    return (
        <div className="App">
        <CurrentUserContext.Provider value={currentUser} >
            <div className="root">
                <Header email={myemail} onSignOut={onSignOut} />
                <Switch >
                    <Route path="/Auth/sign-in" >
                        <Login onLogin={onLogin} />
                    </Route>

                    <Route path="/Auth/sign-up" >
                        <Register onRegister={onRegister} />                       
                    </Route>

                    <ProtectedRoute
                        path="/"
                        loggedIn={loggedIn}
                        component={Main}
                        cards={cards}
                        onCardLike={handleCardLike}
                        onCardDelete={handleCardDelete}
                        onCardClick={handleCardClick}
                        onEditAvatar={handleEditAvatarClick}
                        onEditProfile={handleEditProfileClick}
                        onAddPlace={handleAddPlaceClick}
                    />
                </Switch>
            </div>
            <EditProfilePopup isOpen={isEditProfilePopupOpen}
                onClose={closeAllPopups}
                onUpdateUser={handleUpdateUser}
            />
            <EditAvatarPopup isOpen={isEditAvatarPopupOpen}
                onClose={closeAllPopups}
                onUpdateAvatar={handleUpdateAvatar}
            />
            <AddPlacePopup isOpen={isAddPlacePopupOpen}
                onClose={closeAllPopups}
                onAddPlace={handleAddPlaceSubmit}
            />
            <ImagePopup card={selectedCard}
                onClose={closeAllPopups}
            />
            <InfoTooltip isOpen={isInfoTooltipOpen}
                            onClose={closePopupNew}
                            text={text}
                            logo={logo}
                        />
        </CurrentUserContext.Provider>
    </div>
    );
}

export default withRouter(App);